import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataTransferService } from 'src/app/services/data-transfer.service';

@Component({
  selector: 'app-welcome-login',
  templateUrl: './welcome-login.page.html',
  styleUrls: ['./welcome-login.page.scss'],
})
export class WelcomeLoginPage implements OnInit {
  homePageData: any;

  constructor(private router:Router,private sendData:DataTransferService) {
    this.homePageData=this.sendData.alldata;
  
   }

  ngOnInit() {
  }
  goToNext(){
    this.sendData.alldata=this.homePageData;
    this.router.navigate(['/upload-profile-picture']);
  }

}
